<template>
    <div
        class="portfolio-block"
        :class="[{
            'is-horisontal': isHorizontal
        }]"
        :style="{
            '--translate-x':(translate + 'px'),
        }"
    >
        <div
            :class="['title', {
            underline: isUnderline, 
            'title-mini': isMini,
        }]"
        >
            <span v-html="title"></span>
        </div>

        <Slide
            v-if="isHorizontal"
            :item-width="408"
            :item-width-mobile="258"
            :desktop-fixed="true"
            class="postfolio-slider-items-list-container"
        >
            <section
                ref="itemsList"
                class="postfolio-slider-items-list"
                :style="{'max-height':maxHeight + 'px'}"
                :class="[{'container-fixed':this.percent !== 0 && this.percent !== 1}]"
            >
                <PortfolioItem
                    :is-preloader="isPreloader"
                    v-for="(l, i) in items"
                    :key="i"
                    :src=" (l.preview === undefined ?l.src: l.preview.src)"
                    :video=" (l.preview === undefined ?l.video: l.preview.video)"
                    :title=" (l.preview === undefined ?l.title: l.preview.title)"
                    :href=" (l.preview === undefined ?l.href: l.preview.href)"
                    :is-light=" (l.preview === undefined ?l.isLight: l.preview.isLight)"
                />
            </section>
        </Slide>

        <div
            ref="scrollContainer"
            v-if="isHorizontal"
            class="postfolio-items-list-container"
            :style="{
                '--translate-x':(translate + 'px'),
                '--container-height':containerHeight
            }"
        >
            <section
                ref="itemsList"
                class="postfolio-items-list"
                :style="{'max-height':maxHeight + 'px'}"
                :class="[{'container-fixed':this.percent !== 0 && this.percent !== 1}]"
            >
                <PortfolioItem
                    :is-preloader="isPreloader"
                    v-for="(l, i) in items"
                    :key="i"
                    :src=" (l.preview === undefined ?l.src: l.preview.src)"
                    :video=" (l.preview === undefined ?l.video: l.preview.video)"
                    :title=" (l.preview === undefined ?l.title: l.preview.title)"
                    :href=" (l.preview === undefined ?l.href: l.preview.href)"
                    :is-light=" (l.preview === undefined ?l.isLight: l.preview.isLight)"
                />
            </section>
        </div>
        <section v-else class="postfolio-items-list">
            <PortfolioItem
                :is-preloader="isPreloader"
                v-for="(l, i) in items"
                :key="i"
                :src=" (l.preview === undefined ?l.src: l.preview.src)"
                :video=" (l.preview === undefined ?l.video: l.preview.video)"
                :title=" (l.preview === undefined ?l.title: l.preview.title)"
                :href=" (l.preview === undefined ?l.href: l.preview.href)"
                :is-light=" (l.preview === undefined ?l.isLight: l.preview.isLight)"
                ref="postfolioItem"
            />
            <template v-if="(isLoadingMore || isPreloader) && isMain">
                <PortfolioItem :is-preloader="true" v-for="(l, i) in 2 " :key="i + 10000"/>
            </template>
        </section>
    </div>
</template>

<script>
import PortfolioItem from "../components/PortfolioItem.vue";
import Slide from "../components/Slide.vue";
export default {
    name: "PortfolioBlock",
    props: {
        isMain: Boolean,
        isMore: Boolean,
        isPreloader: Boolean,
        title: String,
        items: Array,
        isUnderline: Boolean,
        isMini: Boolean,
        isHorizontal: Boolean
    },
    data() {
        return {
            isLoadingMore: false,
            scrollValue: 0,
            translate: 0,
            percent: 0,
            containerHeight: "auto",
            maxHeight: 1200
        };
    },
    computed: {},
    methods: {
        onResize() {
            if (window.innerWidth < 1024) {
                this.maxHeight = window.innerWidth - 28;
            } else {
                this.maxHeight = window.innerWidth - 200;
            }
        },
        onScroll() {
            if (this.$refs.postfolioItem) {
                const el = this.$refs.postfolioItem[
                    this.$refs.postfolioItem.length - 1
                ].$el;
                if (
                    !this.isLoadingMore &&
                    el.getBoundingClientRect().top - window.innerHeight < 0
                ) {
                    this.loadMore();
                }
            }
        },
        loadMore() {
            if (this.isMore) {
                this.isLoadingMore = true;
                this.$emit("loadmore");
            }
        },
        loadMoreFinish() {
            this.isLoadingMore = false;
        }
    },
    mounted() {
        window.addEventListener("resize", this.onResize);
        window.addEventListener("scroll", this.onScroll);
        this.onResize();
        this.onScroll();
    },
    destroyed() {
        window.removeEventListener("resize", this.onResize);
        window.removeEventListener("scroll", this.onScroll);
    },
    components: {
        Slide,
        PortfolioItem
    }
};
</script>

<style lang="scss">
