$portfolio-block-height: var(--portfolio-block-height);
$translate: translateY(var(--translate));
#App-main {
    transform: $translate;
}

.portfolio-block {
    height: $portfolio-block-height;
    box-sizing: border-box;
    overflow: hidden;
    padding: 0;
    display: grid;
    .postfolio-items-list,
    .postfolio-slider-items-list {
        display: grid;
        grid-gap: 8px;
        grid-auto-rows: 1fr;
        @media all and (max-width: $xxl - 1px) {
            grid-template-columns: 1fr 1fr;
        }
        @media all and (max-width: 500px) {
            grid-template-columns: 1fr;
        }
        grid-template-columns: repeat(3, minmax(0, 1fr));

        &::before {
            content: "";
            width: 0;
            padding-bottom: 100%;
            grid-row: 1 / 1;
            grid-column: 1 / 1;
        }

        & > *:first-child {
            grid-row: 1 / 1;
            grid-column: 1 / 1;
        }
    }
    .title {
        @media all and (max-width: $md) {
            font-size: 15px;
        }
        font-family: Axiforma-Bold;
        font-size: 24px;
        margin-bottom: 15px;
    }
    .title-mini {
        @media all and (max-width: $md) {
            font-size: 11px;
                font-size: 13px;
        }
        font-family: Axiforma-SemiBold;
        font-size: 18px;
    }
    .underline {
        text-decoration: none;
        span {
            padding-bottom: 2px;
            border-bottom: 1px solid #ffffff80;
        }
    }

    &.is-horisontal {
        width: 100%;
        .postfolio-items-list-container {
            @media all and (max-width: $md) {
                height: 285px;
            }
            display: grid;
            grid-auto-flow: column;
            position: relative;
            height: 450px;
        }
        .postfolio-items-list,
        .postfolio-slider-items-list {
            &.container-fixed {
                // position:absolute;
                // top:0;
            }
            @media all and (max-width: $md) {
                grid-auto-columns: minmax(250px, 250px);
                grid-auto-rows: 250px;
            }
            transform: translate(var(--translate-x));
            grid-auto-flow: row;
            grid-template-columns: unset;
            grid-auto-columns: minmax(400px, 400px);
            grid-auto-rows: 400px;
            .portfolio-block {
                width: 100%;
            }
            &::before {
                content: unset;
            }
            & > *:first-child {
                grid-row: unset;
                grid-column: unset;
            }
        }
        .postfolio-slider-items-list-container {
            @media all and (max-width: $md) {
                display: grid;
                height: 285px;
            }
            display: none;
        }
        .postfolio-slider-items-list {
            @media all and (max-width: $md) {
                padding: 0 14px;
            }
            grid-auto-flow: column;
        }
        .postfolio-items-list-container {
            @media all and (max-width: $md) {
                display: none;
            }
        }
        .postfolio-items-list {
            position: absolute;
            left: 0;
            top: 0;
            $finalHeightMobile: 250px;
            $finalWidthMobile: 3 * $finalHeightMobile;
            $finalHeight: 400px;
            $finalWidth: 3 * $finalHeight;
            $scrollBarHeight: 1px;
            &::-webkit-scrollbar {
                width: $scrollBarHeight;
                height: $scrollBarHeight;
            }

            &::-webkit-scrollbar-button {
                width: $scrollBarHeight;
                height: $scrollBarHeight;
            }

            @media all and (max-width: $md) {
                width: calc(#{$finalHeightMobile} + #{$scrollBarHeight});
                max-height: $finalWidthMobile;
                transform: rotate(-90deg) translateY(-$finalHeightMobile);
                padding: 340px 0 0 0;
            }
            width: calc(#{$finalHeight} + #{$scrollBarHeight});
            max-height: $finalWidth;
            overflow-y: auto;
            overflow-x: hidden;
            transform: rotate(-90deg) translateY(-$finalHeight);
            transform-origin: right top;
            padding: $finalHeight 0 0 0;
            .portfolio-item {
                @media all and (max-width: $md) {
                    width: $finalHeightMobile;
                    height: $finalHeightMobile;
                    margin: 14px 0 0 0;
                }
                padding: 5px;
                transform: rotate(90deg);
                transform-origin: right top;
                width: $finalHeight;
                height: $finalHeight;
                margin: 100px 0 0 0;
            }
        }
    }
}
