<template>
    <div class="video-player">
        
        <video ref="video" 
            @click.prevent="playClickHandler"
            loop playsinline type='video/mp4'
            preload="auto"
            :poster="thumbnail"
        >
            <source :src="src" type='video/mp4; codecs="avc1.42E01E, mp4a.40.2"'>
            Тег video не поддерживается вашим браузером.
        </video>
        <ButtonComponent
            v-show="isPaused"
            ref="playButton"
            @click.native="playClickHandler"
            :is-mini="true"
            :is-arrow-up="false"
        ><span class="icon-play"></span></ButtonComponent>
    </div>
</template>

<script>
import ButtonComponent from './ButtonComponent.vue'
    export default {
        name:'VideoPlayer',
        components: {
            ButtonComponent,
        },
        props:{
            src:String,
            thumbnail:String,
        },
        data() {
            return {
                isPaused:true,
            }
        },
        methods: {
            playClickHandler() {
                if (this.isPaused) {
                    this.$refs.video.play();
                } else {
                    this.$refs.video.pause();
                }
                this.isPaused = !this.isPaused;
            },
            onPlay() {
            }
        },
        mounted() {
        },
    }
</script>

