<template>
    <div
        class="base-tags-list"
        :class="[{'is-mini':isMini}]"
        v-if="tags.length > 0"
        :style="{'--base-translate':translate}"
    >
        <div class="base-tags-list-container">
            <div class="tags-nav tags-left" v-show="isArrowLeftVisible">
                <a
                    :class="['default', 'button-component', 'mini', 'active', 'nav-button']"
                    href="/"
                    @click.prevent="()=> {onArrowClick(-1)}"
                    :is-arrow-left="true"
                >
                    <div :class="['icon', 'arrow-left']"></div>
                </a>
            </div>

            <div ref="tagsListContainer" class="tags-list-container">
                <ul ref="tagsList" class="tags-list">
                    <li
                        ref="tag"
                        :class="['tag-element',{'selected':(isToggle ? selectedElements.indexOf(index) !== -1 : selectedElement === index)}]"
                        v-for="(item, index) in tags"
                        :key="index"
                    >
                        <a
                            :class="[
                            'default', 
                            'button-component', 
                            'tag-button', 
                            {
                                'inverted':(isToggle ? selectedElements.indexOf(index) !== -1 : selectedElement === index)
                            }]"
                            @click.prevent="()=> {onTagClick(item, index)}"
                        >
                            <span>{{item.title}}</span>
                        </a>
                        <!-- <ButtonComponent
                            class="tag-button"
                            
                            height="33px"
                            font-size="12px"
                        >
                            <span>{{item.title}}</span>
                        </ButtonComponent>-->
                    </li>
                </ul>
            </div>
            <div class="tags-nav tags-right" v-show="isArrowRightVisible">
                <a
                    :class="['default', 'button-component', 'mini', 'active', 'nav-button']"
                    href="/"
                    @click.prevent="()=> {onArrowClick(1)}"
                    :is-arrow-left="true"
                >
                    <div :class="['icon', 'arrow-right']"></div>
                </a>
                <!-- <ButtonComponent
                    :is-mini="true"
                    :class="['active', 'nav-button']"
                    :is-arrow-right="true"
                    @click.prevent.native="() => {onArrowClick(1)}"
                ></ButtonComponent>-->
            </div>
        </div>
    </div>
</template>

<script>
import ButtonComponent from "./ButtonComponent.vue";
export default {
    props: {
        tags: Array,
        isMini: Boolean,
        excludeFirst: Boolean,
        isToggle: Boolean
    },
    data() {
        return {
            currentIndex: 0,
            selectedElements: [0],
            selectedElement: 0,
            isArrowLeftVisible: false,
            isArrowRightVisible: false,
            translate: "translateX(0%)",
            interval: 0
        };
    },
    methods: {
        onArrowClick(offset) {
            if (
                this.currentIndex + offset >= 0 &&
                this.currentIndex + offset <= this.tags.length - 1
            ) {
                this.currentIndex += offset;
                const parentLeft = this.$refs.tagsList.getBoundingClientRect()
                    .left;
                const elemLeft = this.$refs.tag[
                    this.currentIndex
                ].getBoundingClientRect().left;
                const maxOffset =
                    this.$refs.tagsList.clientWidth -
                    this.$refs.tagsListContainer.clientWidth;
                this.translate = `translateX(-${(elemLeft - parentLeft).clamp(
                    0,
                    maxOffset
                )}px)`;
            }
            this.updateArrows();
        },
        updateArrows() {
            this.isArrowLeftVisible = this.currentIndex > 0;
            this.isArrowRightVisible = true;

            if (this.$refs.tagsList !== undefined) {
                const parentLeft = this.$refs.tagsList.getBoundingClientRect()
                    .left;
                const elemLeft = this.$refs.tag[
                    this.currentIndex
                ].getBoundingClientRect().left;
                if (
                    elemLeft -
                        parentLeft +
                        this.$refs.tagsListContainer.clientWidth >=
                    this.$refs.tagsList.clientWidth
                ) {
                    this.isArrowRightVisible = false;
                }
            }
        },
        onTagClick(item, index) {
            if (this.isToggle) {
                const pos = this.selectedElements.indexOf(index);

                if (pos === -1) {
                    this.selectedElements.push(index);
                } else {
                    this.selectedElements.splice(pos, 1);
                }
                if (this.excludeFirst) {
                    if (index !== 0) {
                        const i = this.selectedElements.indexOf(0);

                        if (i !== -1) {
                            this.selectedElements.splice(i, 1);
                        }
                        if (this.selectedElements.length === 0) {
                            this.selectedElements = [0];
                        }
                    } else {
                        this.selectedElements = [0];
                    }
                }
                this.$emit("change", item, index, this.selectedElements);
                const tag = this.selectedElements.map(
                    elem => this.tags[elem].title
                );
                this.$router.push({ path: "/portfolio/", query: { tag: tag } });
            } else {
                this.selectedElement = index;
                this.$emit("change", item, index, this.selectedElement);
                this.$router.push({
                    path: "/portfolio/",
                    query: { tag: item.title }
                });
            }
        },
        update() {
            if (this.isToggle) {
                this.selectedElements = [];
                this.tags.forEach((element, index) => {
                    if (
                        this.$route.query.tag &&
                        this.$route.query.tag.indexOf(element.title) !== -1
                    ) {
                        this.selectedElements.push(index);
                    }
                });
                if (this.selectedElements.length === 0) {
                    this.selectedElements = [0];
                }
            } else {
                this.tags.forEach((element, index) => {
                    if (
                        this.$route.query.tag &&
                        this.$route.query.tag.indexOf(element.title) !== -1
                    ) {
                        this.selectedElement = index;
                    }
                });
            }
        }
    },
    watch: {
        $route() {
            this.update();
        }
    },
    mounted() {
        if (this.isToggle && typeof this.selectedElement === "number") {
            this.selectedElement = [this.selectedElement];
        }
        this.update();
        this.updateArrows();
    },
    components: {
        ButtonComponent
    }
};
</script>

