            transition: transform 0.5s;
            list-style-type: none;
            margin: 0;
            padding: 0;
            display: grid;
            grid-gap: 15px;
            grid-auto-flow: column;
            justify-content: flex-start;
            width: max-content;
            li.tag-element {
                @media all and (max-width: $md) {
                    display: inline-block;
                    height: 33px;
                    margin-bottom: 14px;
                    margin-left: 14px;
                }
                .tag-button {
                    @media all and (max-width: $md) {
                        display: inline-block;
                        height: 33px;
                        line-height: 34px;
                        font-size: 11px;
                font-size: 13px;
                    }
                    height: 42px;
                    line-height: 42px;
                    display: block;
                }
                margin: 0;
                padding: 0;
            }
        }
    }

    .tag-button {
        padding: 0 20px !important;
    }
    .tag-button.base-button.default {
        border-radius: 17px;
        background: $base-bg-color;
    }
