                #ffffffff 40px,
                #ffffff00 100%
            );
            right: 0px;
            justify-content: flex-end;
        }
    }
    &.is-mini {
        .tags-list-container {
            .tag-button {
                @media all and (max-width: $md) {
                    font-size: 11px;
