                        line-height: 34px;
                    }
                }
                background: transparent;
                border: 1px solid #24314180;
                &:hover {
                    span {
                        color: #fff;
                    }
                    color: #fff;
                    background: #243141;
                    border-color: transparent;
                }
                box-sizing: border-box;
                color: #24314180;
                span {
                    color: #24314180;
                }
            }
            .selected {
                .tag-button {
                    &:hover {
                        span {
