<template>
    <div class="portfolio-page">
        <BasicTemplate v-if="data">
            <Header
                theme="mini"
                :logo="data.logo"
                :button="data.button"
                :list="data.list"
                title="<strong>Портфолио</strong>"
                :isUnderline="false"
            ></Header>
            <TagsList
                v-if="tags"
                :exclude-first="true"
                :tags="tags.list"
                :is-toggle="true"
                @change="onTagChange"
            ></TagsList>

            <div class="portfolio-container">
                <PortfolioBlock
                    :key="key"
                    :is-more="isMore"
                    :is-main="true"
                    :is-preloader="isLoading"
                    class="portfolio"
                    ref="portfolio"
                    v-if="data"
                    :items="data"
                    :is-mini="true"
                    @loadmore="loadMore"
                />
            </div>
        </BasicTemplate>
    </div>
</template>

<script>
import Header from "../containers/Header";
import PortfolioBlock from "../containers/PortfolioBlock";
import TagsList from "../components/TagsList.vue";
import BasicTemplate from "./BasicTemplate";
import AppData from "../AppData";
import utils from "../utils";

export default {
    name: "Portfolio",
    data() {
        return {
            key: 0,
            isLoading: false,
            isMore: true,
            data: [], //AppData.portfolio,
            last: undefined,
            utils: utils,
            tags: undefined, //AppData.portfolio,
            where: [],
            metaTitle: undefined,
            metaDescription: undefined,
            metaImage: undefined,
            metaKeywords: undefined,
            metaImageWidth: 1200,
            metaImageHeight: 630
        };
    },
    metaInfo() {
        return {
            title: this.metaTitle,
            meta: [
                { name: "title", property: "title", content: this.metaTitle },
                {
                    name: "description",
                    property: "description",
                    content: this.metaDescription
                },
                {
                    name: "keywords",
                    property: "keywords",
                    content: this.metaKeywords
                },
                {
                    name: "og:title",
                    property: "og:title",
                    content: this.metaTitle
                },
                {
                    name: "og:description",
                    property: "og:description",
                    content: this.metaDescription
                },
                {
                    name: "og:image",
                    property: "og:image",
                    content: this.metaImage
                },
                {
                    name: "og:image:width",
                    property: "og:image:width",
                    content: this.metaImageWidth
                },
                {
                    name: "og:image:height",
                    property: "og:image:height",
                    content: this.metaImageHeight
                },
                {
                    name: "twitter:card",
                    property: "twitter:card",
                    content: "summary_large_image"
                },
                {
                    name: "twitter:image",
                    property: "twitter:image",
                    content: this.metaImage
                }
            ]
        };
    },
    mounted() {
        AppData.fetchDoc("portfolio", "meta").then(result => {
            let meta = result.data();
            this.metaTitle = meta.title;
            this.metaDescription = meta.description;
            this.metaKeywords = meta.keywords;
            this.metaImage = meta.img.src;
            setTimeout(() => {
                document.dispatchEvent(new Event("x-app-rendered"));
            }, 5000);
        });
        AppData.fetchDoc("portfolio", "tags").then(result => {
            this.tags = result.data();
            var routeTags = this.$route.query.tag;
            if (routeTags) {
                var selected = [];
                this.tags.list.forEach((element, index) => {
                    if (routeTags.indexOf(element.title) !== -1) {
                        selected.push(index);
                    }
                });
                this.onTagChange(0, 0, selected);
            } else {
                this.loadMore();
            }
            setTimeout(() => {
                document.dispatchEvent(new Event("x-app-rendered"));
            }, 5000);
        });
    },
    methods: {
        onTagChange(item, index, selected) {
            this.isLoading = true;
            this.where = [];
            this.isMore = true;
            this.last = undefined;
            this.data = [];
            this.key++;
            if (selected.length > 0 && selected[0] === 0) {
                this.isLoadingMore = true;
                this.loading = false;
                this.loadMore();
            } else {
                this.isLoadingMore = true;
                selected.forEach(element => {
                    this.loading = false;
                    let query = {
                        field: "tags",
                        condition: "array-contains",
                        value: this.tags.list[element].title
                    };
                    this.where.push(query);
                    this.loadMore(query, 100);
                });
            }
        },
        loadMore(where, limit = 10) {
            if (this.isMore && !this.loading) {
                AppData.fetchData("portfolios", limit, this.last, where)
                    .then(result => {
                        var data = [];
                        for (let key in result.data) {
                            data.push(result.data[key]);
                        }
                        if (data.length < limit) {
                            this.isMore = false;
                        }
                        this.last = result.last;

                        if (!this.data) {
                            this.data = [];
                        }
                        let tempData = this.data.concat(data);
                        if (this.where.length !== 0) {
                            tempData = tempData.filter(item => {
                                var flag = true;
                                this.where.forEach(element => {
                                    if (
                                        item.tags.indexOf(element.value) === -1
                                    ) {
                                        flag = false;
                                    }
                                });
                                return flag;
                            });
                            let unique = [];
                            tempData.forEach(item => {
                                let founded = unique.find(element => {
                                    return element.id === item.id;
                                });
                                if (!founded) {
                                    unique.push(item);
                                }
                            });
                            tempData = unique;
                        }
                        this.data = tempData;
                        this.isLoading = false;
                        if (this.$refs.portfolio) {
                            this.$refs.portfolio.loadMoreFinish();
                        }
                        this.isLoadingMore = false;
                    })
                    .catch(err => {
                        if (!this.data) {
                            this.data = [];
                        }
                        this.isLoading = false;
                        if (this.$refs.portfolio) {
                            this.$refs.portfolio.loadMoreFinish();
                        }
                        this.isLoadingMore = false;
                    });
            } else {
                this.$refs.portfolio.loadMoreFinish();
                this.isLoadingMore = false;
            }
        }
    },
    components: {
        Header,
        PortfolioBlock,
        TagsList,
        BasicTemplate
    },
    beforeRouteEnter(to, from, next) {
        if (from.name === null) {
            next();
            return;
        }
        window.dispatchEvent(new Event("pageLoading"));
        window.dispatchEvent(new Event("pageLoaded"));
        next();
    }
};
</script>

