<template>
    <div class="adv-page">
        <BasicTemplate>
            <Header
                v-if="data"
                theme="full"
                :logo="data.logo"
                :button="data.header.button"
                :list="data.banner.list"
                :isUnderline="false"
            ></Header>

            <Solutions
                v-if="data && loadedExamples"
                class="show-onscroll"
                v-scroll="utils.handleScroll"
                :title="data.solutions.description.title"
                :desc="data.solutions.description.desc"
                :banner="data.solutions.banner"
                :blocks="data.solutions.blocks"
                :no-more="true"
            />
            <template v-if="data && loadedExamples">
                <Example
                    class="design-example"
                    v-for="(l,i) in data.examples.list"
                    :key="i"
                    :data="l"
                    :theme="'light'"
                />
            </template>
        </BasicTemplate>
    </div>
</template>

<script>
import Header from "../containers/Header";
import Solutions from "../containers/Solutions";
import Example from "../containers/Example.vue";
import BasicTemplate from "./BasicTemplate";
import AppData from "../AppData";
import utils from "../utils";

export default {
    name: "Adv",
    data() {
        return {
            data: undefined, //AppData.adv,
            utils: utils,
            metaTitle: undefined,
            metaDescription: undefined,
            metaKeywords: undefined,
            metaImage: undefined,
            loadedExamples: false,
            metaImageWidth: 1200,
            metaImageHeight: 630
        };
    },
    metaInfo() {
        return {
            title: this.metaTitle,
            meta: [
                { name: "title", property: "title", content: this.metaTitle },
                {
                    name: "description",
                    property: "description",
                    content: this.metaDescription
                },
                {
                    name: "keywords",
                    property: "keywords",
                    content: this.metaKeywords
                },
                {
                    name: "og:title",
                    property: "og:title",
                    content: this.metaTitle
                },
                {
                    name: "og:description",
                    property: "og:description",
                    content: this.metaDescription
                },
                {
                    name: "og:image",
                    property: "og:image",
                    content: this.metaImage
                },
                {
                    name: "og:image:width",
                    property: "og:image:width",
                    content: this.metaImageWidth
                },
                {
                    name: "og:image:height",
                    property: "og:image:height",
                    content: this.metaImageHeight
                },
                {
                    name: "twitter:card",
                    property: "twitter:card",
                    content: "summary_large_image"
                },
                {
                    name: "twitter:image",
                    property: "twitter:image",
                    content: this.metaImage
                }
            ]
        };
    },
    mounted() {
        AppData.fetchData("adv")
            .then(result => {
                this.data = result;
                this.metaTitle = this.data.meta.title;
                this.metaDescription = this.data.meta.description;
                this.metaKeywords = this.data.meta.keywords;
                this.metaImage = this.data.meta.img.src;
                setTimeout(() => {
                    document.dispatchEvent(new Event("x-app-rendered"));
                }, 5000);
                let countToLoad =
                    result.examples.list[0].portfolio.items.length;
                result.examples.list[0].portfolio.items.forEach(
                    (element, index) => {
                        var el = element;
                        var i = index;
                        AppData.fetchDoc(
                            "portfolios",
                            el.href.replace(/\//g, "")
                        )
                            .then(portfilio => {
                                countToLoad--;
                                result.examples.list[0].portfolio.items[
                                    i
                                ] = portfilio.data().preview;
                                if (countToLoad == 0) {
                                    this.loadedExamples = true;
                                }
                            })
                            .catch(err => {
                                countToLoad--;
                                if (countToLoad == 0) {
                                    this.loadedExamples = true;
                                }
                            });
                    }
                );
            })
            .catch(() => {
                this.data = AppData.adv;
            });
    },
    methods: {},
    components: {
        Header,
        Solutions,
        Example,
        BasicTemplate
    },
    beforeRouteEnter(to, from, next) {
        if (from.name === null) {
            next();
            return;
        }
        window.dispatchEvent(new Event("pageLoading"));
        AppData.fetchData("adv")
            .then(result => {
                window.dispatchEvent(new Event("pageLoaded"));
                next();
            })
            .catch(() => {
                window.dispatchEvent(new Event("pageLoaded"));
                next();
            });
    }
};
</script>

<style lang="scss">
.adv-page {
    .App-example {
        padding-top: 10px;
    }
}
</style>