<template>
    <div>
        <div class="carousel" ref="carousel">
            <v-touch
                class="carousel"
                @panstart="movedHandler"
                @panmove="movingHandler"
                @panend="endHandler"
                :pan-options="{ direction: 'horizontal', threshold:40 }"
            >
                <div
                    class="carousel-container"
                    ref="carouselContainer"
                    :class="[{'is-touching':isTouching}]"
                    :style="{
                        '--transform':translate,
                        '--transform-mobile':translateMobile,
                        '--transition-duration':transitionDuration,
                        '--ease':ease,
                    }"
                >
                    <section class="partners-carousel-list" v-for="(l,i) in list" :key="i">
                        <a v-if="l.href" :href="l.href" target="_blank" rel="nofollow">
                            <img class="img-banner" :src="l.img.src" :style="styleImg" />
                            <div class="content" v-if="l.text">
                                <slot :text="l.text"></slot>
                            </div>
                        </a>
                        <template v-else>
                            <img class="img-banner" :src="l.img.src" :style="styleImg" />
                            <div class="content" v-if="l.text">
                                <slot :text="l.text"></slot>
                            </div>
                        </template>
                    </section>
                </div>
            </v-touch>
        </div>
        <ul class="nav-carousel" v-show="nav_count > 1">
            <li
                v-for="(l,i) in this.nav_count"
                :class="['default',{'active':nav_current === i}]"
                :id="i"
                :key="i"
                @click.prevent="()=> selectNav(i)"
            ></li>
        </ul>
    </div>
</template>

<script>
export default {
    name: "Carousel",
    props: {
        list: Array,
        styleImg: Object
    },
    data() {
        return {
            itemsPerPage: 10,
            nav_count: 0,
            nav_current: 0,
            translate: "",
            translateMobile: "",
            ease: "ease-in-out",
            transform: "translate(0px)",
            transformCurrent: 0,
            transformPixels: 0,
            isTouching: false,
            moveStart: 0,
            transitionDuration: "0.25s",
            currentPage: 0
        };
    },
    mounted() {
        window.addEventListener("resize", this.onResize);
        this.onResize();
    },

    watch: {
        nav_current(val) {
            this.update(val);
        }
    },
    methods: {
        onResize() {
            if (screen.width < 1024) {
                this.itemsPerPage = Math.floor(screen.width / 150) * 2;
            } else {
                this.itemsPerPage = 10;
            }
            if (this.list !== "undefined" && this.list.length > 0) {
                this.nav_count =
                    Math.floor(this.list.length / this.itemsPerPage) +
                    (this.list.length % this.itemsPerPage === 0 ? 0 : 1);
            }
            if (this.nav_current.clamp) {
                this.nav_current = this.nav_current.clamp(
                    0,
                    this.nav_count - 1
                );
            }
        },
        selectNav: function(index) {
            this.ease = "ease-in-out";
            this.nav_current = index;
        },

        movedHandler(e) {
            this.isTouching = true;
            this.moveStart = e.center.x;
        },
        movingHandler(e) {
            if (this.isTouching) {
                if (this.moveStart !== 0) {
                    this.transformCurrent =
                        this.transformPixels - (this.moveStart - e.center.x);
                    this.translateMobile = `translateX(${this.transformCurrent}px)`;
                }
            }
        },
        endHandler(e) {
            const lastNav = this.nav_current;
            this.ease = "ease-out";
            const itemWidth = (150 * this.itemsPerPage) / 2;
            this.isTouching = false;
            const diff = this.moveStart - e.center.x;
            let swipeCount = Math.round(diff / itemWidth);
            if (Math.abs(e.overallVelocity) > 1) {
                swipeCount += Math.sign(diff);
            }
            if (swipeCount === 0 && diff < 0) {
                swipeCount = -1;
            }
            if (swipeCount === 0 && diff > 0) {
                swipeCount = 1;
            }

            this.nav_current += swipeCount;
            this.nav_current = this.nav_current.clamp(0, this.nav_count - 1);
            if (this.nav_current === lastNav) {
                this.update(lastNav);
            }
            //this.nav_current = Math.round(-this.transformPixels / itemWidth)
        },
        update(val) {
            if (val === 0) {
                this.translate = `translateX(0%)`;
                this.translateMobile = `translateX(0px)`;
                this.transformPixels = 0;
            } else {
                this.translate = `translateX(-${val * 100}%)`;
                const pageWidth = 150 * (this.itemsPerPage / 2);
                const offset = -pageWidth * val;
                const maxTranslate =
                    this.$refs.carousel.clientWidth -
                    this.$refs.carouselContainer.clientWidth;
                if (offset.clamp) {
                    this.transformPixels = offset.clamp(maxTranslate, 0);
                    this.translateMobile = `translateX(${this.transformPixels}px)`;
                    this.transitionDuration = "0.25s";
                }
            }
        }
    }
};
</script>

<style lang="scss">
$transform: var(--transform);
$transform-mobile: var(--transform-mobile);
$ease: var(--ease);
$transition-duration: var(--transition-duration);
.carousel {
    @media all and (max-width: $md) {
        display: grid;
        grid-template-columns: minmax(0, 1fr);
    }
    .carousel-container {
        @media all and (max-width: $md) {
            padding: 0 14px;
            grid-auto-columns: 150px;
            transform: $transform-mobile;
        }
        display: grid;
        grid-auto-flow: column;
        grid-template-rows: repeat(2, minmax(0, 63px));
        grid-auto-columns: minmax(20%, max-content);
        grid-gap: 35px 0;
        justify-content: space-between;
        justify-self: flex-start;
        transform: $transform;
        transition: transform $transition-duration $ease;
        &.is-touching {
            transition: none;
        }
    }
    overflow-y: hidden;
    overflow-x: hidden;

    .partners-carousel-list {
        // Добавлен grid что не выходил за рамки png
        // Убрать - найти другое решение
        display: grid;
        grid-template-columns: 1fr;
        a {
            display:grid;
        }

        .img-banner {
            justify-self: center;
            align-self: center;
            max-width: 103px;
            max-height: 63px;
            object-fit: contain;
            z-index: 1;
            grid-column: 1;
            grid-row: 1;
        }

        .content {
            z-index: 2;
            grid-column: 1;
            grid-row: 1;
        }
    }
}

.nav-carousel {
    @media all and (max-width: $md) {
        padding: 0 14px;
        grid-auto-flow: column;
        grid-gap: 0;
        grid-auto-columns: minmax(0, 1fr);
        grid-template-columns: unset;
    }
    margin-top: 50px;

    display: grid;
    grid-template-columns: repeat(auto-fill, 65px);
    grid-column-gap: 25px;
    list-style-type: 0;
    margin-left: 0;
    margin-right: 0;
    padding: 0;
    li {
        display: block;
        justify-self: flex-start;
        align-self: center;
        width: 100%;
        padding: 10px 0;
        border-bottom: 2px solid #fff;
        border-radius: 0;
    }

    .default {
        @media all and (max-width: $md) {
            border-color: #dddddd;
        }
        border-color: #798599;
    }

    .active {
        @media all and (max-width: $md) {
            border-color: #46b2e8;
        }
        border-color: #243141;
    }
}
</style>