<template>
    <div :class="['portfolio-item', {'is-light':isLight}]">
        <a :href="href" class="item-link" @dragstart.prevent="()=>{return false}">
            <div class="title" v-html="title" v-if="!isPreloader" v-show="!preloadData"></div>
            <ButtonComponent
                v-if="!isPreloader"
                v-show="!preloadData"
                class="btn-more"
                :is-mini="true"
                :is-arrow-right="true"
            ></ButtonComponent>

            <video
                v-if="!isPreloader && video"
                v-show="!preloadData"
                class="video"
                muted
                autoplay
                playsinline
                webkit-playsinline
                loop
            >
                <source :src="video" />
            </video>
            <img
                v-show="!preloadData"
                v-if="!isPreloader && src"
                :src="src"
                @dragstart.prevent="()=>{return false}"
            />

            <ContentLoader
                class="content-loader"
                v-if="isPreloader || preloadData"
                :height="160"
                :width="400"
                :speed="2"
                primaryColor="#E0DFE0"
                secondaryColor="#ecebeb"
                preserveAspectRatio="xMinYMin meet"
                style="background:#F7F7FE"
            >
                <rect x="28" y="40" rx="0" ry="0" width="166" height="20" />
                <rect x="28" y="70" rx="0" ry="0" width="228" height="20" />
            </ContentLoader>
        </a>
    </div>
</template>

<script>
import { ContentLoader } from "vue-content-loader";
import ButtonComponent from "../components/ButtonComponent";
export default {
    name: "PortfolioItem",
    props: {
        title: String,
        href: String,
        src: String,
        video: String,
        isLight: Boolean,
        isPreloader: Boolean
    },
    components: {
        ContentLoader,
        ButtonComponent
    },
    data() {
        return {
            preloadData: true
        };
    },
    watch: {
        title() {
            this.preloadData = true;
            setTimeout(() => {
                this.preloadData = false;
            }, 1000);
        }
    },
    methods: {},
    mounted() {
        this.preloadData = true;
        setTimeout(() => {
            this.preloadData = false;
        }, 1000);
        // console.log(this.video);
    }
};
</script>

