<template>
    <div id="App-callback">
        <Description
            v-scroll="handleScroll"
            id="desc-callback"
            :title="description.title"
            :desc="description.desc"
        />
        <form v-if="!formSent" id="form-callback" v-scroll="handleScroll">
            <InputComponent
                id="phone"
                name="phone"
                :text-default="form.title"
                @input="onInput"
                v-model="phone"
                :valid="phone !== ''"
                :class="[{'error':errorField,}]"
            />
            <ButtonComponent
                id="button-callback"
                :is-transparent="true"
                :is-deactive="buttonDisabled"
                @click.native="onClickButton"
            >{{form.btn.title}}</ButtonComponent>
            <input
                type="checkbox"
                style="display:none"
                id="agree"
                checked="true"
                @change="onChangeCheckbox"
                @input="onChangeCheckbox"
            />
            <label id="form-agreement" v-html="form.agreement" for="agree"></label>
        </form>
        <div v-else class="form-sent">
            <i class="icon-phone"></i>
            <span v-html="form.sent.desc"></span>
        </div>
        <div class="image-container">
            <div class="img" id="bg"  v-scroll="handleScroll" />
        </div>
    </div>
</template>

<script>
import Description from "../components/Description";
import ButtonComponent from "../components/ButtonComponent";
import InputComponent from "../components/InputComponent";
import utils from "../utils";
import AppData from "../AppData";
export default {
    name: "Callback",
    props: {
        description: Object,
        sent: Object,
        form: Object
    },
    data() {
        return {
            formSent: false,
            buttonDisabled: false,
            utils: utils,
            errorField: false,
            phone: ""
        };
    },
    components: {
        Description,
        ButtonComponent,
        InputComponent
    },
    methods: {
        onClickButton() {
            const result =
                this.phone.indexOf("_") !== -1 || this.phone.length === 0;
            this.errorField = result;
            if (!this.errorField) {
                AppData.callRequest(this.phone)
                    .then(() => {
                        this.formSent = true;
                    })
                    .catch(() => {});
            }
        },
        onChangeCheckbox(e) {
            this.buttonDisabled = !e.target.checked;
        },
        onInput(e) {
            this.errorField = false;
            this.phone = e.target.value;
        },
        handleScroll: function(evt, el) {
            if (window.scrollY > 3190) {
                el.setAttribute("style", "animation: showUp 1s forwards;");
            }
            //1290
            //2290
            //2620
            //3190
            //
            return window.scrollY > 100;
        }
    }
};
</script>

