<template>
    <div id="App-solutions">
        <Description id="description-solutions" :title="title" :desc="desc" />
        <div class="empty-block-1" />
        <div class="container show-onscroll" ref="container" v-scroll="utils.handleScroll">
            <div class="container-full">
                <Slide :item-width="275" :pages-count="blocks.length" :desktop-fixed="true">
                    <div class="container-slidable">
                        <div class="block" :class="['block-' + i]" :key="i" v-for="(b,i) in blocks" @click="() => {openUrl(b.url, b.isExternal)}">
                            <Description
                                class="desc-solutions"
                                :title="b.title"
                                :desc="b.desc"
                                :is-mini="true"
                            />
                            <span>
                                <ButtonComponent
                                    class="nav-link"
                                    :url="(b.url.indexOf('\/\/') == -1) ? '//' + b.url : b.url "
                                    :is-mini="true"
                                    v-if="b.isExternal"
                                    :target="b.isExternal ? '_blank':undefined"
                                    :is-arrow-right="true"
                                ></ButtonComponent>
                            </span>
                            <ButtonComponent
                                class="tag-button"
                                color="#fff"
                                height="33px"
                                font-size="12px"
                                v-if="b.comingSoon"
                                :is-inverted="false"
                            >Скоро</ButtonComponent>
                            <span>
                                <ButtonComponent
                                    class="btn-solutions"
                                    :url="(b.isExternal  && b.url.indexOf('\/\/') == -1) ? '//' + b.url : b.url"
                                    :is-mini="true"
                                    v-if="!b.comingSoon && !noMore"
                                    :target="b.isExternal ? '_blank':undefined"
                                    :is-arrow-right="true"
                                ></ButtonComponent>
                            </span>
                            <span>
                                <a
                                    class="site-link"
                                    v-if="b.link"
                                    :href="(b.isExternal && b.link.indexOf('\/\/') == -1) ? '//' + b.link: b.link "
                                    :target="b.isExternal ? '_blank':undefined"
                                >{{b.link}}</a>
                            </span>
                            <div v-if="noMore"></div>
                        </div>
                        <div class="block block-banner" v-if="banner && banner.isVisible">
                            <Banner
                                theme="solution"
                                v-if="banner"
                                :list="banner.list"
                                :style-img="styleImage"
                            ></Banner>
                        </div>
                    </div>
                </Slide>
            </div>
        </div>
    </div>
</template>

<script>
import Slide from "../components/Slide.vue";
import Banner from "../components/Banner";
import Description from "../components/Description";
import ButtonComponent from "../components/ButtonComponent";
import utils from "../utils";
export default {
    name: "Solutions",
    data() {
        return {
            styleImage: {
                "object-fit": "cover",
                "object-position": "0% 0%",
                width: "100%",
                height: "100%"
            },
            utils: utils
        };
    },
    props: {
        noMore: Boolean,
        title: String,
        desc: String,
        blocks: Array,
        banner: Object
    },
    methods: {
        onEnterFinish() {
            setTimeout(() => {
                utils.handleScroll(undefined, this.$el);
                utils.handleScroll(undefined, this.$refs.container);
            }, 300);
        },
        openUrl(url, isExternal) {
            if (!isExternal) {
                if (url.indexOf("https://") !== -1 || url.indexOf("http://") !== -1) {
                    window.open( url, '_blank');
                } else {
                    this.$router.push(url)
                }
            }
        },
    },
    created() {
        window.addEventListener("afterEnter", this.onEnterFinish);
    },
    destroyed() {
        window.removeEventListener("afterEnter", this.onEnterFinish);
    },
    mounted() {
        utils.handleScroll(undefined, this.$el);
        utils.handleScroll(undefined, this.$refs.container);
    },
    components: {
        Slide,
        Banner,
        Description,
        ButtonComponent
    }
};
</script>

