<template>
    <div class="app-container" v-if="footer && callback">
        <slot></slot>
        <Callback :description="callback.description" :form="callback.form" />
        <Footer
            :descsription="footer.info.desc"
            :policy="footer.info.policy"
            :policy-href="footer.info.href"
        />
    </div>
</template>

<script>
import Callback from "../containers/Callback";
import Footer from "../containers/Footer";
import AppData from "../AppData";
import utils from "../utils";
export default {
    name: "BasicTemplate",
    data() {
        return {
            callback: undefined, //AppData.callback,
            footer: undefined //AppData.footer,
        };
    },
    mounted() {
        AppData.fetchData("callback")
            .then(result => {
                this.callback = result;
            })
            .catch(() => {
                this.callback = AppData.callback;
            });
        AppData.fetchData("footer")
            .then(result => {
                this.footer = result;
            })
            .catch(() => {
                this.footer = AppData.footer;
            });
        if (this.$route.hash) {
            setTimeout(() => {
                var el = document.getElementById(
                    this.$route.hash.replace("#", "") + "-nav"
                );
                if (el) {
                    utils.scrollTo(el);
                }
            }, 1000);
            return { x: 0, y: 0 };
        }
    },
    methods: {
        handleAppSolutions: function(evt, el) {
            if (window.scrollY > 50) {
                el.setAttribute("style", "animation: showUp 1s forwards;");
            }
            //1290
            //2290
            //2620
            //3190
            //
            return window.scrollY > 100;
        },
        handleAppPoll: function(evt, el) {
            if (window.scrollY > 1290) {
                el.setAttribute("style", "animation: showUp 1s forwards;");
            }
        },
        handleAppFeatures: function(evt, el) {
            if (window.scrollY > 2620) {
                el.setAttribute("style", "animation: showUp 1s forwards;");
            }
        }
    },
    components: {
        Callback,
        Footer
    }
};
</script>

<style lang="scss">
.app-container {
    background: #fff;
    width: 100vw;
    overflow: hidden;
}
</style>