<template>
    <div
        class="App-nav-container"
        :style="{
            '--box-shadow':`0 0 40px 0px rgba(0,0,0, ${shadow})`
        }"
    >
        <nav class="App-nav" :class="[{'opened':opened}]">
            <ul class="menu">
                <li v-for="(v,i) in menu" :key="i" :class="[ {'inactive':v.subMenu}]">
                    <router-link
                        tag="a"
                        v-if="!v.subMenu"
                        :to="v.href"
                        :href="v.href"
                        class="menu-element active"
                        v-html="v.title"
                        @click.native="onClick"
                    ></router-link>
                    <a
                        v-else
                        tag="a"
                        :to="v.href"
                        :href="v.href"
                        class="menu-element inactive"
                        v-html="v.title"
                    ></a>
                    <ul v-if="v.subMenu" class="submenu">
                        <li v-for="(s,i) in v.subMenu" :key="i" class="submenu-element">
                            <!-- :style="{'--content-v':''.concat('\'0',(i+1),'\'')}" -->
                            <router-link
                                tag="a"
                                :to="s.href"
                                :href="s.href"
                                v-html="s.title"
                                class="active"
                                @click.native="onClick"
                            ></router-link>
                        </li>
                    </ul>
                </li>
                <ul class="contacts">
                    <li v-for="(v,i) in contacts" :key="i">
                        <a v-html="v" class="active" :class="[{'email': i === 1}]" target="_blank"></a>
                    </li>
                </ul>
                <div class="link">
                    <a class="download" target="_blank" :href="download.link">
                        <span>
                            <u>{{download.title}}</u>
                        </span>
                    </a>
                </div>
            </ul>
            <ButtonComponent
                :url="'/'"
                id="button-to-top"
                class="button-move-top"
                :class="[{visible:isButtonToTopVisible}]"
                :is-mini="true"
                :is-arrow-up="true"
                v-scroll="handleScroll"
                @click.prevent.native="scrollToTop"
            />
        </nav>

        <div class="menu-buttons" :class="[{'opened':opened}]" v-if="!hideHamburger">
            <hamburger
                class="hamburger"
                :stroke="1"
                :gap="5"
                color="#fff"
                :width="14"
                :open.sync="opened"
            ></hamburger>
        </div>
    </div>
</template>

<script>
import {
    disableBodyScroll,
    enableBodyScroll,
    clearAllBodyScrollLocks
} from "body-scroll-lock";
import "vue-hamburger/index.css";
import hamburger from "vue-hamburger";
import ButtonComponent from "../components/ButtonComponent";
import { setTimeout } from "timers";
export default {
    name: "Nav",
    props: {
        hideHamburger: Boolean,
        menu: Array,
        contacts: Array,
        download: Object
    },
    data() {
        return {
            shadow: 0,
            opened: false,
            isButtonToTopVisible: false,
            targetElements: []
        };
    },
    watch: {
        opened(val) {
            if (val) {
                this.targetElement.forEach(element => {
                    element.classList.add("stop-scrolling");
                    disableBodyScroll(element);
                });
            } else {
                this.targetElement.forEach(element => {
                    element.classList.remove("stop-scrolling");
                    enableBodyScroll(element);
                });
            }
        },
        $route() {
            this.isButtonToTopVisible = false;
            setTimeout(() => {
                var elems = document.getElementsByClassName("current-page");
                Array.from(elems).forEach(element => {
                    element.removeEventListener("scroll", this.handleScroll);
                    element.addEventListener("scroll", this.handleScroll);
                });
            }, 500);
        }
    },
    methods: {
        onClick() {
            this.opened = false;
            this.targetElement.forEach(element => {
                element.classList.remove("stop-scrolling");
                enableBodyScroll(element);
            });
        },
        onScroll() {
            this.shadow = 0.2 * (window.scrollY.clamp(0, 800) / 800);
        },
        scrollToTop() {
            (function smoothscroll() {
                var elem = document.getElementById("current-page");
                var currentScroll = elem.scrollTop;
                if (currentScroll > 0) {
                    window.requestAnimationFrame(smoothscroll);
                    document
                        .getElementById("current-page")
                        .scrollTo(0, currentScroll - currentScroll / 5);
                }
            })();
        },
        handleScroll() {
            var elems = document.getElementsByClassName("current-page");
            Array.from(elems).forEach(element => {
                this.isButtonToTopVisible =
                    element.scrollTop > element.getBoundingClientRect().height;
            });
        }
    },
    mounted() {
        this.targetElement = Array.from(
            document.getElementsByClassName("current-page")
        );
        this.targetElement.forEach(element => {
            element.addEventListener("scroll", this.onScroll);
        });
    },
    destroyed() {
        this.targetElement.forEach(element => {
            element.removeEventListener("scroll", this.onScroll);
        });
        clearAllBodyScrollLocks();
    },
    components: {
        hamburger,
        ButtonComponent
    }
};
</script>

