<template>
    <header id="App-header" :class="[`theme-${theme}`]">
        <router-link to="/" class="logo-header"></router-link>

        <div v-if="title" :class="['title-header']">
            <span v-html="title"></span>
            <vue-typer
                v-if="typeEffect"
                ref="vueTyperTitle"
                class="title-header"
                @completed="onTitleComplete"
                :erase-delay="50"
                erase-style="backspace"
                :text="titleTyper"
            ></vue-typer>
        </div>

        <div class="banner-header" v-if="theme !== 'mini'">
            <Banner
                :theme="theme"
                :list="list"
                :isUnderline="isUnderline"
                :button="button"
                :style-img="this.data.styleImage"
            ></Banner>
        </div>
    </header>
</template>

<script>
import { VueTyper } from "vue-typer";
import Banner from "../components/Banner";
export default {
    name: "Header",
    data() {
        return {
            data: {
                styleImage: {
                    "min-height": "294px",
                    "object-fit": "cover",
                    "object-position": "0% 0%",
                    width: "100%",
                    height: "100%"
                },
                titleCompleted: false
            }
        };
    },

    methods: {
        onShow() {
            if (this.$refs.vueTyperTitle !== undefined) {
                this.titleCompleted = false;
                this.$refs.vueTyperTitle.reset();
                this.$refs.vueTyperTitle.cancelCurrentAction();
                setTimeout(() => {
                    this.$refs.vueTyperTitle.init();
                }, 900);
            }
        },
        onTitleComplete() {
            this.titleCompleted = true;
            // this.$refs.vueTyperDescription.reset();
        }
    },
    mounted() {
        if (this.$refs.vueTyperTitle !== undefined) {
            this.titleCompleted = false;
            this.$refs.vueTyperTitle.reset();
            this.$refs.vueTyperTitle.cancelCurrentAction();
            this.onShow();
        }
    },
    props: {
        isMain: Boolean,
        theme: {
            default: "default"
        },
        isUnderline: {
            default: true
        },
        title: String,
        list: Array,
        button: {
            default: "Подробнее",
            type: String
        },
        typeEffect: Boolean,
        titleTyper: Array || String
    },
    components: {
        Banner,
        VueTyper
    }
};
</script>

