export default {
  handleScroll: function(evt, el) {
    var elems = document.getElementsByClassName("current-page");
    Array.from(elems).forEach(element => {
      if (
        el.getBoundingClientRect().y <
        element.getBoundingClientRect().height / 1.0
      ) {
        el.setAttribute("style", "animation: showUp 1s forwards;");
      }
    });
  },
  animating: false,
  getOffset(el, base) {
    const rect = el.getBoundingClientRect();
    return {
      left: rect.left + base.scrollLeft,
      top: rect.top + base.scrollTop
    };
  },
  easeInOut(t) {
    return t < 0.5 ? 4 * t * t * t : (t - 1) * (2 * t - 2) * (2 * t - 2) + 1;
  },
  scrollTo(el) {
    var elems = document.getElementsByClassName("current-page");
    Array.from(elems).forEach(element => {
      if (!this.animating) {
        this.animating = true;
        let offset = this.getOffset(el, element);
        let start = element.scrollTop;
        let end = offset.top;
        let currentFrame = 0;
        let smoothscroll = () => {
          if (currentFrame < 60) {
            window.requestAnimationFrame(smoothscroll);
            currentFrame++;
            var t = currentFrame / 60;
            element.scrollTo(0, start + this.easeInOut(t) * (end - start));
          } else {
            this.animating = false;
          }
        };
        smoothscroll();
      } 
    });
  }
};
