<template>
    <div class="page-contacts">
        <div class="app-container">
            <Header
                v-if="data"
                theme="mini"
                :logo="data.logo"
                :button="data.button"
                :list="data.list"
                title="<strong>Контакты</strong>"
                :isUnderline="false"
            ></Header>
            <div class="page-contacts-content" v-if="data">
                <div class="contact phone">
                    <strong v-html="data.phone.label"></strong>
                    {{data.phone.value }}
                </div>
                <div class="contact email">
                    <strong v-html="data.email.label"></strong>
                    <a :href="'mailto:' + data.email.value">
                        <span>{{data.email.value }}</span>
                    </a>
                </div>
                <div class="contact address">
                    <strong v-html="data.address.label"></strong>
                    <a target="blank" :href="data.address.href">
                        <span v-html="data.address.value"></span>
                        <i class="icon-external"></i>
                    </a>
                </div>
                <div class="map">
                    <iframe
                        :src="data.address.iframe.href"
                        width="100%"
                        :height="mapSize"
                        frameborder="0"
                    ></iframe>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
import Header from "../containers/Header";
import utils from "../utils";
import AppData from "../AppData";

export default {
    name: "Design",
    data() {
        return {
            data: undefined, //AppData.main,
            utils: utils,
            mapSize: 240,
            metaTitle: undefined,
            metaDescription: undefined,
            metaImage: undefined,
            metaKeywords: undefined,
            metaImageWidth: 1200,
            metaImageHeight: 630
        };
    },
    metaInfo() {
        return {
            title: this.metaTitle,
            meta: [
                { name: "title", property: "title", content: this.metaTitle },
                {
                    name: "description",
                    property: "description",
                    content: this.metaDescription
                },
                {
                    name: "keywords",
                    property: "keywords",
                    content: this.metaKeywords
                },
                {
                    name: "og:title",
                    property: "og:title",
                    content: this.metaTitle
                },
                {
                    name: "og:description",
                    property: "og:description",
                    content: this.metaDescription
                },
                {
                    name: "og:image",
                    property: "og:image",
                    content: this.metaImage
                },
                {
                    name: "og:image:width",
                    property: "og:image:width",
                    content: this.metaImageWidth
                },
                {
                    name: "og:image:height",
                    property: "og:image:height",
                    content: this.metaImageHeight
                },
                {
                    name: "twitter:card",
                    property: "twitter:card",
                    content: "summary_large_image"
                },
                {
                    name: "twitter:image",
                    property: "twitter:image",
                    content: this.metaImage
                }
            ]
        };
    },
    methods: {
        onResize() {
            if (window.innerWidth <= 1500) {
                this.mapSize = 240;
            } else {
                this.mapSize = 480;
            }
        }
    },
    mounted() {
        document.getElementById("App-main").classList.add("contacts");

        AppData.fetchData("contacts")
            .then(result => {
                this.data = result;
                this.metaTitle = this.data.meta.title;
                this.metaDescription = this.data.meta.description;
                this.metaKeywords = this.data.meta.keywords;
                this.metaImage = this.data.meta.img.src;
                setTimeout(() => {
                    document.dispatchEvent(new Event("x-app-rendered"));
                }, 5000);
            })
            .catch(() => {
                this.data = AppData.main;
            });

        window.addEventListener("resize", this.onResize);
        if (window.innerWidth <= 1500) {
            this.mapSize = 240;
        } else {
            this.mapSize = 480;
        }
    },
    destroyed() {
        document.getElementById("App-main").classList.remove("contacts");
        window.removeEventListener("resize", this.onResize);
    },
    components: {
        Header
    },
    beforeRouteEnter(to, from, next) {
        if (from.name === null) {
            next();
            return;
        }
        window.dispatchEvent(new Event("pageLoading"));
        AppData.fetchData("contacts")
            .then(result => {
                window.dispatchEvent(new Event("pageLoaded"));
                next();
            })
            .catch(() => {
                window.dispatchEvent(new Event("pageLoaded"));
                next();
            });
    }
};
</script>

