import Vue from "vue";
import VueRouter from "vue-router";

import Home from "./pages/Home.vue";
import Strategy from "./pages/Strategy.vue";
import Design from "./pages/Design.vue";
import It from "./pages/It.vue";
import Video from "./pages/Video.vue";
import Adv from "./pages/Adv.vue";
import Portfolio from "./pages/Portfolio.vue";
import PortfolioFull from "./pages/PortfolioFull.vue";
import Contacts from "./pages/Contacts.vue";
import utils from "./utils";

Vue.use(VueRouter);

const router = new VueRouter({
  mode: "history",
  base: "",
  routes: [
    {
      path: "/",
      name: "Home",
      component: Home
    },
    {
      path: "/strategy",
      name: "Strategy",
      component: Strategy
    },
    {
      path: "/design",
      name: "Design",
      component: Design
    },
    {
      path: "/it",
      name: "It",
      component: It
    },
    {
      path: "/video",
      name: "Video",
      component: Video
    },
    {
      path: "/adv",
      name: "Adv",
      component: Adv
    },
    {
      path: "/portfolio",
      name: "Portfolio",
      component: Portfolio
    },
    {
      path: "/portfolio/:id(\\d+)",
      name: "PortfolioFull",
      component: PortfolioFull
    },
    {
      path: "/contacts",
      name: "Contacts",
      component: Contacts
    }
  ],
  scrollBehavior(to, from, savedPosition) {
    if (to.hash) {
      if (savedPosition) {
        return savedPosition;
      } else {
        if (from.path === to.path) {
          let start =
            document.documentElement.scrollTop || document.body.scrollTop;
          setTimeout(() => {
            var el = document.getElementById(to.hash.replace("#", "") + "-nav");
            if (el) {
              utils.scrollTo(el);
            }
          }, 100);
          return { x: 0, y: start };
        } else {
          setTimeout(() => {
            let timer = setInterval(() => {
              var el = document.getElementById(to.hash.replace("#", "") + "-nav");
              if (el && !utils.animating) {
                clearInterval(timer)
                utils.scrollTo(el);
              }
            }, 50);
          }, 1000);
          return { x: 0, y: 0 };
        }
      }
    } else if (savedPosition) {
      // setTimeout(() => {
      //   utils.scrollToPosition(el);
      // }, 500);
      return savedPosition;
    } else {
      if (to.path === from.path && to.hash === "" && from.hash !== "") {
        return savedPosition;
      } else {
        return { x: 0, y: 0 };
      }
    }
  }
});

