<template>
    <div id="app">
        <div id="App-main">
            <Nav
                v-if="navigation"
                :download="navigation.download"
                :menu="navigation.menu.list"
                :contacts="navigation.contacts.list"
            />
            <div
                class="preloader"
                :class="[{'animating':animating}]"
                :style="{'--progress':(progress + '%'), '--top':top} "
            ></div>
            <transition name="slide" @after-enter="afterEnter">
                <router-view
                    class="page current-page"
                    id="current-page"
                    sticky-container
                    :style="{'--origin-y':originY} "
                ></router-view>
            </transition>
        </div>
    </div>
</template>

<script>
import Nav from "./components/Nav";
import AppData from "./AppData";
import { setTimeout, setInterval, clearInterval } from "timers";
export default {
    name: "solorumgroup",
    components: { Nav },
    data() {
        return {
            navigation: undefined, //AppData.navigation
            progress: 0,
            top: 0,
            interval: false,
            animating: false,
            originY: 0,
            currentPage: undefined
        };
    },
    methods: {
        updateTouchScroll() {
            var els = document.getElementsByClassName("page");
            this.allowUp = true;
            this.allowDown = true;
            Array.from(els).forEach(el => {
                el.addEventListener("touchstart", function(event) {
                    this.allowUp = this.scrollTop > 0;
                    this.allowDown =
                        this.scrollTop < this.scrollHeight - this.clientHeight;
                    this.slideBeginY = event.pageY;
                    if (event.touches.length > 0) {
                        this.slideBeginY = event.touches[0].pageY;
                    }
                });

                el.addEventListener("touchmove", function(event) {
                    var pageY = event.pageY;
                    if (event.touches.length > 0) {
                        pageY = event.touches[0].pageY;
                    }
                    var up = pageY > this.slideBeginY;
                    var down = pageY < this.slideBeginY;
                    this.slideBeginY = pageY;
                    if ((up && this.allowUp) || (down && this.allowDown)) {
                        event.stopPropagation();
                    } else {
                        event.preventDefault();
                    }
                });
            });
        },
        afterEnter() {
            window.dispatchEvent(new Event("afterEnter"));
            clearInterval(this.interval);

            this.top = "-5px";
            setTimeout(() => {
                this.progress = 0;
            }, 500);

            this.updateTouchScroll();
        },
        pageLoading() {
            this.currentPage = document
                .getElementById("current-page")
                .getElementsByClassName("app-container")[0];
            this.originY = -this.currentPage.getBoundingClientRect().y + "px";
            this.top = "0";
            clearInterval(this.interval);
            this.interval = setInterval(() => {
                if (this.progress < 80) {
                    this.progress += 2.5;
                }
            }, 10);
        },
        pageLoaded() {
            clearInterval(this.interval);
            this.interval = setInterval(() => {
                if (this.progress < 100) {
                    this.progress += 2.5;
                }
            }, 10);
        }
    },
    created() {
        window.addEventListener("pageLoading", this.pageLoading);
        window.addEventListener("pageLoaded", this.pageLoaded);
    },
    destroyed() {
        window.removeEventListener("pageLoading", this.pageLoading);
        window.removeEventListener("pageLoaded", this.pageLoaded);
    },
    mounted() {
        AppData.fetchData("navigation")
            .then(result => {
                this.navigation = result;
            })
            .catch(() => {
                this.navigation = AppData.navigation;
            });
        this.updateTouchScroll();
    }
};
</script>

