<template>
    <div :class="['banner', `theme-${theme}`]">
        <section
            class="list-section-baner"
            v-for="(l,i) in list"
            :key="i"
            :style="{
                     'transform':translate
                 }"
        >
            <video
                class="img-banner"
                muted
                autoplay
                playsinline
                webkit-playsinline
                loop
                v-if="l.video"
                :poster="l.video.thumbnail"
                :style="styleImg"
            >
                <source :src="l.video.src" />
            </video>
            <img class="img-banner" v-else-if="l.img" :src="l.img.src" :style="styleImg" />
            <div class="content" v-if="l.text" :class="[{'no-button': !l.button ||  !l.isButton}]">
                <Description
                    ref="bannerDescription"
                    :typeEffect="isMain"
                    id="description-header"
                    :more="l.text.more"
                    :title="l.text.title"
                    :desc="l.text.desc"
                    :isUnderline="isUnderline"
                />
                <ButtonComponent
                    v-if="l.button && l.isButton"
                    id="button-header"
                    :url="l.url"
                >{{l.button}}</ButtonComponent>
            </div>
        </section>
        <ButtonComponent
            v-if="list.length > 1"
            id="button-prev"
            :class="['active', 'nav-button', {'deactive': (currentBanner === 0)}]"
            :is-mini="true"
            :is-arrow-left="true"
            :is-white="true"
            @click.native="() => {bannerChange(-1)}"
        ></ButtonComponent>
        <ButtonComponent
            v-if="list.length > 1"
            id="button-next"
            :class="['active', 'nav-button', {'deactive': (currentBanner === (list.length - 1))}]"
            :is-mini="true"
            :is-arrow-right="true"
            :is-white="true"
            @click.native="() => {bannerChange(1)}"
        ></ButtonComponent>
    </div>
</template>

<script>
import ButtonComponent from "../components/ButtonComponent";
import Description from "../components/Description";
export default {
    name: "Banner",
    props: {
        isMain: Boolean,
        theme: {
            default: "default"
        },
        list: Array,
        isUnderline: {
            default: true
        },
        button: {
            default: "Подробнее",
            type: String
        },
        styleImg: Object
    },
    data() {
        return {
            currentBanner: 0
        };
    },
    computed: {
        translate() {
            return `translateX(-${this.currentBanner * 100}%)`;
        }
    },
    methods: {
        bannerChange: function(offset) {
            this.currentBanner = (this.currentBanner + offset).clamp(
                0,
                this.list.length - 1
            );
        }
    },
    components: {
        ButtonComponent,
        Description
    }
};
</script>

