                        />
                        <div class="video-container">
                            <VideoPlayer
                                v-if="data.design.video.src"
                                :src="data.design.video"
                                :thumbnail="data.design.thumbnail"
                            ></VideoPlayer>
                            <img v-else :src="data.design.image.src" />
