                        />
                    </div>
                </div>

                <div class="block-info block-info-full devices">
                    <div class="block-info-content">
                        <h2 class="block-title" v-html="data.devices.title"></h2>
                    </div>
                    <div class="device-container" ref="deviceContainer">
                        <img class="device-frame" :src="data.devices.deviceFrame" />
                        <Slide
                            :item-width="246"
