<script>
import Nav from "../components/Nav";
import Header from "../containers/Header";
import PortfolioBlock from "../containers/PortfolioBlock";
import Description from "../components/Description.vue";
import VideoPlayer from "../components/VideoPlayer.vue";
import MasonryGallery from "../components/MasonryGallery.vue";
import NavPanel from "../components/NavPanel.vue";

import TagsList from "../components/TagsList.vue";
import BasicTemplate from "./BasicTemplate";
import AppData from "../AppData";
import utils from "../utils";
import Slide from "../components/Slide";

export default {
    name: "Portfolio",
    data() {
        return {
            data: undefined, // AppData.portfolios.portfolio1,
            doc: undefined, // AppData.portfolios.portfolio1,
            navigation: undefined, //AppData.navigation,
            utils: utils,
            isBottom: false,
            currentScreen: 2,
            paddingBottom: "0px",
            footerOffset: "0px",
            translate: "translateX(0px)",
            deviceWidth: 246,
            deviceHeight: 506,
            offsetX: 0,
            prev: undefined,
            next: undefined,
            portfolios: undefined,
            metaTitle: undefined,
            metaDescription: undefined,
            metaImage: undefined,
            metaKeywords: undefined,
            metaImageWidth: 1200,
            metaImageHeight: 630
        };
    },
    metaInfo() {
        return {
            title: this.metaTitle,
            meta: [
                { name: "title", property: "title", content: this.metaTitle },
                {
                    name: "description",
                    property: "description",
                    content: this.metaDescription
                },
                {
                    name: "keywords",
                    property: "keywords",
                    content: this.metaKeywords
                },
                {
                    name: "og:title",
                    property: "og:title",
                    content: this.metaTitle
                },
                {
                    name: "og:description",
                    property: "og:description",
                    content: this.metaDescription
                },
                {
                    name: "og:image",
                    property: "og:image",
                    content: this.metaImage
                },
                {
                    name: "og:image:width",
                    property: "og:image:width",
                    content: this.metaImageWidth
                },
                {
                    name: "og:image:height",
                    property: "og:image:height",
                    content: this.metaImageHeight
                },
                {
                    name: "twitter:card",
                    property: "twitter:card",
                    content: "summary_large_image"
                },
                {
                    name: "twitter:image",
                    property: "twitter:image",
                    content: this.metaImage
                }
            ]
        };
    },
    computed: {
        tags() {
            if (this.data) {
                return this.data.tags.map(item => {
                    return { title: item };
                });
            } else {
                return [];
            }
        },
        prevPage() {
            return this.prev;
        },
        nextPage() {
            return this.next;
        }
    },
    watch: {
        $route() {
            this.fetchData();
        }
    },
    created() {},
    mounted() {
        // console.log(document.getElementById("App-main"));
        document.getElementById("App-main").classList.add("portfolio");
        this.fetchData();
        // this.onResize();
        AppData.fetchData("navigation")
            .then(result => {
                this.navigation = result;
            })
            .catch(() => {
                this.navigation = AppData.navigation;
            });
    },
    beforeUpdate() {
        this.onResize();
    },
    updated() {
        this.onResize();
    },
    destroyed() {
        document.getElementById("App-main").classList.remove("portfolio");
        var elems = document.getElementsByClassName("current-page");
        Array.from(elems).forEach(element => {
            element.removeEventListener("scroll", this.onScroll);
        });
        window.removeEventListener("scroll", this.onScroll);
        window.removeEventListener("resize", this.onResize);
        window.removeEventListener("scroll", this.onResize);
    },
    methods: {
        fetchData() {
            this.data = undefined;
            this.doc = undefined;

            let id = parseInt(this.$route.params.id);
            if (id) {
                AppData.fetchDoc("portfolios", "portfolio" + id)
                    .then(result => {
                        // this.metaTitle = result.meta.title;
                        // this.metaDescription = result.meta.description;
                        // this.metaKeywords = result.meta.keywords;
                        this.data = result.data();
                        this.metaTitle = this.data.meta.title;
                        this.metaDescription = this.data.meta.description;
                        this.metaKeywords = this.data.meta.keywords;
                        this.metaImage = this.data.meta.img.src;
                        this.updateScrollEvents();
                        this.fetchPrev(result);
                        this.fetchNext(result);
                        this.fetchSimiliar(result);
                        setTimeout(() => {
                            document.dispatchEvent(new Event("x-app-rendered"));
                        }, 5000);
                        // this.$forceUpdate();
                        // this.$nextTick(() => {
                        //     this.onResize();
                        // });
                        // setTimeout(this.onResize, 500);
                    })
                    .catch(() => {
                        this.data = undefined;
                    });
            }
        },
        updateScrollEvents() {
            var elems = document.getElementsByClassName("current-page");
            Array.from(elems).forEach(element => {
                element.removeEventListener("scroll", this.onScroll);
                element.addEventListener("scroll", this.onScroll);
            });
        },
        fetchSimiliar() {
            AppData.fetchData("portfolios", 9)
                .then(result => {
                    var data = [];
                    for (let key in result.data) {
                        data.push(result.data[key]);
                    }
                    data = data.filter(item => {
                        return item.id !== this.data.id;
                    });
                    this.portfolios = data;
                })
                .catch(() => {
                    // console.log(err);
                });
        },
        fetchPrev(doc) {
            AppData.fetchDoc("portfolios", null, doc)
                .then(result => {
                    // console.log(result);
                    if (result.docs[0]) {
                        this.prev = "/portfolio/" + result.docs[0].data().id;
                    } else {
                        this.prev = undefined;
                    }
                })
                .catch(() => {
                    // console.log(err);
                    this.prev = undefined;
                });
        },
        fetchNext(doc) {
            AppData.fetchDoc("portfolios", null, null, doc)
                .then(result => {
                    // console.log(result);
                    if (result.docs[0]) {
                        this.next = "/portfolio/" + result.docs[0].data().id;
                    } else {
                        this.next = undefined;
                    }
                })
                .catch(() => {
                    // console.log(err);
                    this.next = undefined;
                });
        },
        onResize() {
            if (this.$refs.deviceContainer) {
                this.offsetX = (
                    (this.$refs.deviceContainer.clientWidth -
                        this.$refs.screensList.clientWidth) /
                    2
                ).clamp(0, Number.MAX_VALUE);
            }
        },
        onScroll() {
            this.onResize();
            // let footerOffset = 300;
            // let container = this.$refs.template.$el.getBoundingClientRect();
            // const portfolioRect = document.getElementById('portfolio-block').getBoundingClientRect()
            // const heightCallback = document.getElementById('App-callback').getBoundingClientRect().height
            // const heightFooter = document.getElementById('App-footer').getBoundingClientRect().height
            // if (!this.isBottom && (container.y + container.height <= window.innerHeight + footerOffset) ){
            //     this.isBottom = true;
            // } else if (this.isBottom && (container.y + container.height + (heightCallback + heightFooter) >= window.innerHeight + footerOffset)) {
            //     this.isBottom = false;
            // }
            // this.paddingBottom = heightCallback + heightFooter + 'px';
            // if (this.isBottom) {
            //     this.footerOffset = -footerOffset + 100 + 'px'
            // }
            // if (portfolioRect.y < 190) {
            //     this.footerOffset = (-200 - (portfolioRect.y - 190)).clamp(-200, 100) + 'px'
            // }
            // // console.log(portfolioRect);
            // if (this.isBottom) {
            // } else {
            //     this.paddingBottom = portfolioRect.y
            // }
        }
    },
    components: {
        Header,
        PortfolioBlock,
        TagsList,
        BasicTemplate,
        Description,
        VideoPlayer,
        MasonryGallery,
        NavPanel,
        Slide,
        Nav
    }
};
</script>

