<template>
    <div class="masonry-grid-container">
        <div
            ref="masonryGrid"
            class="masonry-grid"
            :style="{
                'grid-auto-rows':autoRows,
                'grid-row-gap':rowGap
            }"
        >
            <div
                class="item"
                ref="items"
                v-for="(l, i) in items"
                :key="i"
                v-images-loaded:on.progress="imageProgress"
            >
                <div class="content" ref="content">
                    <img :src="l.src" :alt="l.alt">
                </div>
            </div>
        </div>
    </div>
</template>

<script>
import imagesLoaded from "vue-images-loaded";
export default {
    name: "MasonryGallery",
    props: {
        items: Array
    },
    directives: {
        imagesLoaded
    },
    data() {
        return {
            autoRows: "40px",
            rowGap: "20px"
        };
    },
    methods: {
        resizeAllGridItems() {
            const rowHeight = parseInt(this.autoRows);
            const rowGap = parseInt(this.rowGap);
            this.$refs.items.forEach((item, index) => {
                const rowSpan = Math.ceil(
                    (this.$refs.content[index].getBoundingClientRect().height +
                        rowGap) /
                        (rowHeight + rowGap)
                );
                item.style.gridRowEnd = "span " + rowSpan;
            });
        },
        imageProgress() {
            this.resizeAllGridItems();
        }
    },
    mounted() {
        window.addEventListener("resize", this.resizeAllGridItems);
        this.resizeAllGridItems();
    }
};
</script>

<style lang="scss">
.masonry-grid-container {
    @media all and (max-width: $md) {
        overflow-x: scroll;
        width: calc(100vw);
        margin: 0 -14px;
        grid-template-columns: 14px 1fr 14px;
        display: grid;
        -webkit-overflow-scrolling: touch;
        &::-webkit-scrollbar {
            width: 0px;
            background: transparent; /* make scrollbar transparent */
        }
    }
}
.masonry-grid {
    @media all and (max-width: $md) {
        grid-column: 2;
        padding-right: 14px;
        margin-right: -14px;
        grid-template-columns: repeat(2, 260px);
    }
    display: grid;
    grid-gap: 30px;
    grid-template-columns: repeat(3, minmax(0, 1fr));
    grid-auto-rows: 40px;
    align-content: flex-start;
    align-items: flex-start;
    grid-gap: 30px;
    grid-auto-rows: 40px;
    .item {
        background-color: #ffffff;
    }
    img {
        width: 100%;
        height: 100%;
        object-fit: contain;
    }
}
</style>