<template>
    <div class="it-page" v-if="data">
        <BasicTemplate>
            <Header
                theme="full"
                :logo="data.logo"
                :button="data.header.button"
                :list="data.banner.list"
                :isUnderline="false"
            ></Header>

            <Solutions
                class="show-onscroll"
                v-scroll="utils.handleScroll"
                :title="data.solutions.description.title"
                :desc="data.solutions.description.desc"
                :banner="data.solutions.banner"
                :blocks="data.solutions.blocks"
            />

            <Example
                class="design-example"
                v-for="(l,i) in data.examples.list"
                :key="i"
                :data="l"
                :id="l.id"
                :theme="i % 2 === 0 ? 'default' : 'light'"
            />

            <Features
                id="support-nav"
                class="show-onscroll"
                v-scroll="utils.handleScroll"
                :description="data.features.description"
                :list="data.features.list"
            />

            <Example class="design-example" :data="data.govserctor" :theme="'light'" />
        </BasicTemplate>
    </div>
</template>

<script>
import Header from "../containers/Header";
import Solutions from "../containers/Solutions";
import Example from "../containers/Example.vue";
import Features from "../containers/Features.vue";
import BasicTemplate from "./BasicTemplate";
import AppData from "../AppData";
import utils from "../utils";

export default {
    name: "It",
    data() {
        return {
            data: undefined, // AppData.it,
            utils: utils,
            metaTitle: undefined,
            metaDescription: undefined,
            metaImage: undefined,
            metaKeywords: undefined,
            metaImageWidth: 1200,
            metaImageHeight: 630
        };
    },
    metaInfo() {
        return {
            title: this.metaTitle,
            meta: [
                { name: "title", property: "title", content: this.metaTitle },
                {
                    name: "description",
                    property: "description",
                    content: this.metaDescription
                },
                {
                    name: "keywords",
                    property: "keywords",
                    content: this.metaKeywords
                },
                {
                    name: "og:title",
                    property: "og:title",
                    content: this.metaTitle
                },
                {
                    name: "og:description",
                    property: "og:description",
                    content: this.metaDescription
                },
                {
                    name: "og:image",
                    property: "og:image",
                    content: this.metaImage
                },
                {
                    name: "og:image:width",
                    property: "og:image:width",
                    content: this.metaImageWidth
                },
                {
                    name: "og:image:height",
                    property: "og:image:height",
                    content: this.metaImageHeight
                },
                {
                    name: "twitter:card",
                    property: "twitter:card",
                    content: "summary_large_image"
                },
                {
                    name: "twitter:image",
                    property: "twitter:image",
                    content: this.metaImage
                }
            ]
        };
    },
    mounted() {
        AppData.fetchData("it")
            .then(result => {
                this.data = result;
                this.metaTitle = this.data.meta.title;
                this.metaDescription = this.data.meta.description;
                this.metaKeywords = this.data.meta.keywords;
                this.metaImage = this.data.meta.img.src;
                setTimeout(() => {
                    document.dispatchEvent(new Event("x-app-rendered"));
                }, 5000);
            })
            .catch(() => {
                this.data = AppData.it;
            });
    },
    methods: {},
    components: {
        Header,
        Solutions,
        Example,
        Features,
        BasicTemplate
    },
    beforeRouteEnter(to, from, next) {
        if (from.name === null) {
            next();
            return;
        }
        window.dispatchEvent(new Event("pageLoading"));
        AppData.fetchData("it")
            .then(result => {
                window.dispatchEvent(new Event("pageLoaded"));
                next();
            })
            .catch(() => {
                window.dispatchEvent(new Event("pageLoaded"));
                next();
            });
    }
};
</script>

