import Vue from "vue";
import SolorumGroup from "./SolorumGroup.vue";
import router from "./router.js";
import VueTouch from "vue-touch";
import Iconmoon from "./assets/iconmoon/style.css";
import Meta from "vue-meta";

Vue.config.productionTip = false;
VueTouch.config.swipe = {
  direction: "horizontal"
};
Vue.use(VueTouch, { name: "v-touch" });
Vue.use(Meta);

Vue.directive("scroll", {
  inserted: function(el, binding) {
    let f = function(evt) {
      if (binding.value(evt, el)) {
        var elems = document.getElementsByClassName("current-page");
        Array.from(elems).forEach(element => {
          element.removeEventListener("scroll", f);
        });
      }
    };
    var elems = document.getElementsByClassName("current-page");
    Array.from(elems).forEach(element => {
      element.removeEventListener("scroll", f);
      element.addEventListener("scroll", f);
    });
  }
});

new Vue({
  Iconmoon,
  router,
  el: "#app",
  render() {
    return <SolorumGroup logo={this.logo} />;
  }
});

if (!Number.prototype.clamp) {
  // eslint-disable-next-line no-extend-native
  Number.prototype.clamp = function(min, max) {
    return Math.min(Math.max(this, min), max);
  };
}
