<template>
    <div class="nav-panel" ref="navPanel" v-sticky sticky-z-index="10000" v-if="data && navigation">
        <div class="nav-panel-container" ref="navPanel">
            <div class="title" v-html="title"></div>
            <ul class="social-links">
                <li class="social-item" v-for="(l, i) in social" :key="i">
                    <a :href="l.href">
                        <span class="social-icon" :class="l.icon"></span>
                    </a>
                </li>
            </ul>
            <div class="nav-links">
                <ButtonComponent
                    :is-deactive="prevPage === undefined"
                    :url="prevPage"
                    :is-mini="true"
                    :is-arrow-left="true"
                    :is-white="true"
                    @click.native="onLeftClick"
                ></ButtonComponent>
                <ButtonComponent
                    :is-deactive="nextPage === undefined"
                    :url="nextPage"
                    :is-mini="true"
                    :is-arrow-right="true"
                    :is-white="true"
                    @click.native="onRightClick"
                ></ButtonComponent>
            </div>
            <div class="menu-buttons">
                <hamburger
                    class="hamburger"
                    :stroke="1"
                    :gap="5"
                    color="#fff"
                    :width="17"
                    :open.sync="opened"
                    @click.native="onHamburgerClick"
                ></hamburger>
            </div>
        </div>
        <Nav
            :hide-hamburger="true"
            :class="[{'opened':opened}]"
            :download="navigation.download"
            :menu="navigation.menu.list"
            :contacts="navigation.contacts.list"
        />
    </div>
</template>

<script>
import {
    disableBodyScroll,
    enableBodyScroll,
    clearAllBodyScrollLocks
} from "body-scroll-lock";
import "vue-hamburger/index.css";
import hamburger from "vue-hamburger";
import ButtonComponent from "./ButtonComponent.vue";
import Nav from "./Nav.vue";
import Sticky from "vue-sticky-directive";
import AppData from "../AppData";

export default {
    name: "NavPanel",
    props: {
        title: String,
        nextPage: String,
        prevPage: String
    },

    data() {
        return {
            navigation: undefined, //AppData.navigation,
            data: undefined, //AppData.main,
            social: [
                { icon: "icon-vk", href: "/" },
                { icon: "icon-twitter", href: "/" },
                { icon: "icon-fb", href: "/" }
            ],
            opened: false,
            targetElement: []
        };
    },
    mounted() {
        this.targetElement = Array.from(
            document.getElementsByClassName("current-page")
        );
        AppData.fetchData("navigation")
            .then(result => {
                this.navigation = result;
            })
            .catch(() => {
                this.navigation = AppData.navigation;
            });
        AppData.fetchData("main")
            .then(result => {
                this.data = result;
            })
            .catch(() => {
                this.data = AppData.main;
            });
    },
    methods: {
        onHamburgerClick() {
            if (this.opened) {
                this.targetElement.forEach(element => {
                    element.classList.add("stop-scrolling");
                    disableBodyScroll(element);
                });
            } else {
                this.targetElement.forEach(element => {
                    element.classList.remove("stop-scrolling");
                    enableBodyScroll(element);
                });
            }
        },
        onLeftClick() {
            this.$router.push(this.prevPage);
        },
        onRightClick() {
            this.$router.push(this.nextPage);
        }
    },
    destroyed() {
        clearAllBodyScrollLocks();
    },
    directives: {
        Sticky
    },
    components: {
        Nav,
        hamburger,
        ButtonComponent
    }
};
</script>

