  fetchDoc(collection, doc, fetchBefore, fetchAfter) {
    return new Promise((resolve, reject) => {
      try {
        const db = firebase.firestore();
        const settings = {};
        db.settings(settings);
        var data = {};
        var ref = db.collection(collection);

        if (fetchAfter) {
          try {
            // console.log(fetchAfter);
            ref = ref
              .orderBy("id", "asc")
              .startAfter(fetchAfter)
              .limit(1);
          } catch (ex) {
            // console.log(ex);
          }
        } else if (fetchBefore) {
          try {
            // console.log(fetchBefore);
            ref = ref
              .orderBy("id", "desc")
              .startAfter(fetchBefore)
              .limit(1);
          } catch (ex) {
            // console.log(ex);
          }
        } else {
          // console.log(doc);
          ref = ref.doc(doc);
        }

        ref
          .get()
          .then(result => {
            // console.log(result);
            resolve(result);
          })
          .catch(() => {
            // console.log(err);
            data = this[collection][doc];
            resolve(data);
          });
      } catch (err) {
        reject(err);
      }
    });
  },
  cached: {},
  fetchData(collection, limit, last, where) {
    return new Promise((resolve, reject) => {
      try {
        if (this.cached.hasOwnProperty(collection)) {
          resolve(this.cached[collection]);
          return;
        }
        const db = firebase.firestore();
        const settings = {};
        db.settings(settings);
        var data = {};
        var ref = db.collection(collection);

        if (where) {
          ref = ref.where(where.field, where.condition, where.value);
        }
        if (limit) {
          ref = ref.orderBy("id", "asc").limit(limit);
        }
        if (last) {
          ref = ref.startAfter(last);
        }

        ref
          .get()
          .then(result => {
            var last = undefined;
            // // console.log(result);
            result.docs.forEach(element => {
              last = element;
              data[element.id] = element.data();
            });
            if (limit) {
              resolve({ data: data, last: last });
            } else {
              this.cached[collection] = data;
              resolve(data);
            }
          })
          .catch(() => {
            // // console.log(err);
            data = this[collection];
            this.cached[collection] = data;
            data;
          });
      } catch (err) {
        reject(err);
      }
    });
  },
  uploadAppData() {
    const db = firebase.firestore();
    const settings = {};
    db.settings(settings);

    let collections = [
      "main"
      // "strategy",
      // "design",
      // "it",
      // "video",
      // "adv",
      // "portfolio",
      // "portfolios",
      // "navigation",
      // "footer",
      // "callback",
      // "contacts"
    ];
    collections.forEach(element => {
      let data = this[element];
      for (let key in data) {
        if (Array.isArray(data[key])) {
          db.collection(element)
            .doc(key)
            .set({ list: data[key] })
            .then(() => {})
            .catch(() => {});
        } else {
          db.collection(element)
            .doc(key)
            .set(data[key])
            .then(() => {})
            .catch(() => {});
        }
      }
    });
  },
  sendMail(name, email, phone, message) {
    return new Promise((resolve, reject) => {
      const db = firebase.firestore();
      const settings = {};
      db.settings(settings);
      db.collection("mail")
        .add({
          name: name,
          email: email,
          phone: phone,
          message: message,
          date: new Date().getTime()
        })
        .then(result => {
          resolve(result);
        })
        .catch(err => {
          reject(err);
        });
    });
  },
  callRequest(phone) {
    return new Promise((resolve, reject) => {
      const db = firebase.firestore();
      const settings = {};
      db.settings(settings);
      db.collection("calls")
        .add({
          phone: phone,
          date: new Date().getTime()
        })
        .then(result => {
          resolve(result);
        })
        .catch(err => {
          reject(err);
        });
    });
  }
