<template>
    <div class="page-strategy" v-if="data" @scroll="onScroll">
        <BasicTemplate>
            <Header
                class="strategy-header"
                :logo="data.logo"
                :button="data.banner.button"
                :list="data.banner.list"
                :isUnderline="false"
            ></Header>

            <Info
                class="strategy-description"
                :title="data.description.title"
                :desc="data.description.desc"
            ></Info>
            <Poll
                class="show-onscroll"
                ref="poll"
                v-scroll="utils.handleScroll"
                :description="data.poll.description"
                :questions="data.poll.questions"
            />
            <Faq
                class="show-onscroll"
                ref="poll"
                v-scroll="utils.handleScroll"
                :list="data.faq.list"
            />
            <Info
                class="strategy-start"
                :title="data.start.title"
                :desc="data.start.desc"
                :isFull="true"
                theme="light"
            ></Info>
            <div
                class="papers"
                :style="{
                    '--translate':(translate + 'px'),
                }"
            >
                <div class="papers-list" ref="papers">
                    <img :src="papers" />
                    <img :src="papers" />
                    <img :src="papers" />
                    <img :src="papers" />
                    <img :src="papers" />
                </div>
            </div>
            <Info
                class="strategy-result"
                :title="data.result.title"
                :desc="data.result.desc"
                theme="light"
            ></Info>
        </BasicTemplate>
    </div>
</template>

<script>
import Header from "../containers/Header";
import Info from "../containers/Info.vue";
import Poll from "../containers/Poll";
import Faq from "../containers/Faq";
import BasicTemplate from "./BasicTemplate";
import AppData from "../AppData";
import utils from "../utils";
import papers from "../assets/steps@3x.png";

export default {
    name: "Strategy",
    data() {
        return {
            data: AppData.strategy,
            utils: utils,
            papers: papers,
            translate: 0,
            lastScrollY: 0,
            metaTitle: undefined,
            metaDescription: undefined,
            metaImage: undefined,
            metaKeywords: undefined,
            metaImageWidth: 1200,
            metaImageHeight: 630
        };
    },
    metaInfo() {
        return {
            title: this.metaTitle,
            meta: [
                { name: "title", property: "title", content: this.metaTitle },
                {
                    name: "description",
                    property: "description",
                    content: this.metaDescription
                },
                {
                    name: "keywords",
                    property: "keywords",
                    content: this.metaKeywords
                },
                {
                    name: "og:title",
                    property: "og:title",
                    content: this.metaTitle
                },
                {
                    name: "og:description",
                    property: "og:description",
                    content: this.metaDescription
                },
                {
                    name: "og:image",
                    property: "og:image",
                    content: this.metaImage
                },
                {
                    name: "og:image:width",
                    property: "og:image:width",
                    content: this.metaImageWidth
                },
                {
                    name: "og:image:height",
                    property: "og:image:height",
                    content: this.metaImageHeight
                },
                {
                    name: "twitter:card",
                    property: "twitter:card",
                    content: "summary_large_image"
                },
                {
                    name: "twitter:image",
                    property: "twitter:image",
                    content: this.metaImage
                }
            ]
        };
    },
    methods: {
        onScroll() {
            if (this.$refs && this.$refs.papers) {
                var scrollY = document.getElementById("current-page").scrollTop;
                var delta = Math.abs(this.lastScrollY - scrollY);
                if (
                    this.$refs.papers.getBoundingClientRect().y <
                    window.innerHeight
                ) {
                    this.translate += delta;
                    this.translate = this.translate % 935;
                }
                this.lastScrollY = scrollY;
            }
        },
        offset(el) {
            var rect = el.getBoundingClientRect(),
                scrollLeft =
                    window.pageXOffset || document.documentElement.scrollLeft,
                scrollTop =
                    window.pageYOffset || document.documentElement.scrollTop;
            return { top: rect.top + scrollTop, left: rect.left + scrollLeft };
        }
    },
    mounted() {
        AppData.fetchData("strategy")
            .then(result => {
                this.data = result;
                this.metaTitle = this.data.meta.title;
                this.metaDescription = this.data.meta.description;
                this.metaKeywords = this.data.meta.keywords;
                this.metaImage = this.data.meta.img.src;
                setTimeout(() => {
                    document.dispatchEvent(new Event("x-app-rendered"));
                }, 5000);
            })
            .catch(() => {
                this.data = AppData.strategy;
            });
        // document.getElementById('current-page').addEventListener("scroll", this.onScroll);
    },
    destroyed() {
        // if (document.getElementById('current-page')) {
        //     document.getElementById('current-page').removeEventListener("scroll", this.onScroll);
        // }
    },
    components: {
        Header,
        Info,
        Poll,
        Faq,
        BasicTemplate
    },
    beforeRouteEnter(to, from, next) {
        if (from.name === null) {
            next();
            return;
        }
        window.dispatchEvent(new Event("pageLoading"));
        AppData.fetchData("strategy")
            .then(result => {
                window.dispatchEvent(new Event("pageLoaded"));
                next();
            })
            .catch(() => {
                window.dispatchEvent(new Event("pageLoaded"));
                next();
            });
    }
};
</script>

