<template>
    <div>
        <BasicTemplate>
            <Header
                v-if="data"
                :type-effect="true"
                :titleTyper="data.header.titleTyper"
                class="main"
                :isMain="true"
                :logo="data.logo"
                :title="data.header.title"
                :list="data.banner.list"
            />
            <Solutions
                v-if="data"
                :title="data.solutions.description.title"
                :desc="data.solutions.description.desc"
                :blocks="data.solutions.blocks"
            />
            <Poll
                v-if="data"
                class="show-onscroll"
                theme="main"
                v-scroll="utils.handleScroll"
                :description="data.poll.description"
                :questions="data.poll.questions"
            />
            <Partners
                v-if="data"
                :description="data.partners.description"
                :icons="data.partners.icons"
            />
            <Features
                v-if="data"
                theme="main"
                class="show-onscroll"
                v-scroll="utils.handleScroll"
                :description="data.features.description"
                :list="data.features.list"
            />
        </BasicTemplate>
    </div>
</template>

<script>
import Header from "../containers/Header";
import Solutions from "../containers/Solutions";
import Poll from "../containers/Poll";
import Partners from "../containers/Partners";
import Features from "../containers/Features";
import BasicTemplate from "./BasicTemplate";
import AppData from "../AppData";
import utils from "../utils";
export default {
    name: "Home",
    data() {
        return {
            data: undefined, //AppData.main,
            utils: utils,
            metaTitle: undefined,
            metaDescription: undefined,
            metaImage: undefined,
            metaKeywords: undefined,
            metaImageWidth: 1200,
            metaImageHeight: 630
        };
    },
    metaInfo() {
        return {
            title: this.metaTitle,
            meta: [
                {
                    name: "google-site-verification",
                    content: "uiB2b0VtX8Q0wqK1dnZliC_6cfW9PuhlRlyPiEBFbzk"
                },
                { name: "yandex-verification", content: "48bd6d7736e02bd1" },
                { name: "title", property: "title", content: this.metaTitle },
                {
                    name: "description",
                    property: "description",
                    content: this.metaDescription
                },
                {
                    name: "keywords",
                    property: "keywords",
                    content: this.metaKeywords
                },
                {
                    name: "og:title",
                    property: "og:title",
                    content: this.metaTitle
                },
                {
                    name: "og:description",
                    property: "og:description",
                    content: this.metaDescription
                },
                {
                    name: "og:image",
                    property: "og:image",
                    content: this.metaImage
                },
                {
                    name: "og:image:width",
                    property: "og:image:width",
                    content: this.metaImageWidth
                },
                {
                    name: "og:image:height",
                    property: "og:image:height",
                    content: this.metaImageHeight
                },
                {
                    name: "twitter:card",
                    property: "twitter:card",
                    content: "summary_large_image"
                },
                {
                    name: "twitter:image",
                    property: "twitter:image",
                    content: this.metaImage
                },
                {
                    name: "twitter:image",
                    property: "twitter:image",
                    content: this.metaImage
                }
            ]
        };
    },
    methods: {},
    mounted() {
        AppData.fetchData("main")
            .then(result => {
                this.data = result;
                this.metaTitle = this.data.meta.title;
                this.metaDescription = this.data.meta.description;
                this.metaKeywords = this.data.meta.keywords;
                this.metaImage = this.data.meta.img.src;
                setTimeout(() => {
                    document.dispatchEvent(new Event("x-app-rendered"));
                }, 5000);
            })
            .catch(() => {
                this.data = AppData.main;
            });
    },
    components: {
        Header,
        Partners,
        Solutions,
        Poll,
        Features,
        BasicTemplate
    },
    beforeRouteEnter(to, from, next) {
        if (from.name === null) {
            next();
            return;
        }
        window.dispatchEvent(new Event("pageLoading"));
        AppData.fetchData("main")
            .then(result => {
                window.dispatchEvent(new Event("pageLoaded"));
                next();
            })
            .catch(() => {
                window.dispatchEvent(new Event("pageLoaded"));
                next();
            });
    }
};
</script>

<style  lang="scss">
.main {
    .banner-header .description .more {
        @media all and (max-width: $md) {
            margin-bottom: 25px;
            margin-top: 20px;
        }
    }
    .logo-header {
        cursor: default;
    }
}
</style>