<script>
import Description from "../components/Description";
import utils from "../utils";
export default {
    name: "Poll",
    props: {
        theme: String,
        description: Object,
        questions: Array
    },
    methods: {
        onEnterFinish() {
            utils.handleScroll(undefined, this.$el);
        }
    },
    created() {
        window.addEventListener("afterEnter", this.onEnterFinish);
    },
    destroyed() {
        window.removeEventListener("afterEnter", this.onEnterFinish);
    },
    mounted() {
        utils.handleScroll(undefined, this.$el);
    },
    components: {
        Description
    }
};
</script>

